<template>
  <div>
    <el-button type="primary" @click="willAdd">添加</el-button>
    <el-table :data="list" style="width: 100%;margin-bottom: 20px;" row-key="id" border>
      <el-table-column prop="id" label="编号" width="120"></el-table-column>
      <el-table-column prop="title" label="轮播图昵称"></el-table-column>
      <el-table-column prop="phone" label="图片">
         <template slot-scope="scope">
            <img :src="$imgPre+scope.row.img" alt="">
         </template>
      </el-table-column>
      <el-table-column label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status===1">启用</el-button>
          <el-button type="info" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>
    <v-add :info="info" ref="add"></v-add>
  </div>
</template>
<script>
import vAdd from "./components/add.vue";

import { mapActions, mapGetters } from "vuex";
export default {
  components: {
    vAdd
  },
  computed: {
    ...mapGetters({
      list: "loop/loop"
    })
  },
  data() {
    return {
      info: {
        isShow: false,
        title: "添加轮播图"
      },
    };
  },
  methods: {
    ...mapActions({
      requsetLoopList: "loop/requsetLoopList"
    }),
    willAdd() {
      this.info = {
        isShow: true,
        title: "添加轮播图"
      };
    },
    edit(id) {
      this.info = {
        isShow: true,
        title: "编辑轮播图"
      };
      this.$refs.add.getOne(id);
    }
  },
  mounted() {
    this.requsetLoopList();
  }
};
</script>
<style scoped>
img{
   width: 100px;
   height: 100px;
}
</style>


