<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isShow">
      <el-form :model="form">
        <el-form-item label="标题" label-width="120px">
          <el-input v-model="form.title" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="图片" label-width="120px">
          <div class="my-upload">
            <h3>+</h3>
            <img :src="imgUrl" alt v-if="imgUrl" />
            <input type="file" class="ipt" @change="changeFile" />
          </div>
        </el-form-item>

        <el-form-item label="状态" label-width="120px">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.title=='添加轮播图'">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { alertSuccess, alertWarning } from "../../../util/alert";
import { mapActions, mapGetters } from "vuex";
import {
  reqLoopAdd,
  reqLoopListOne,
  reqLoopUpdate
} from "../../../util/request";
export default {
  props: ["info"],
  computed: {
    ...mapGetters({
      list: "loop/loop"
    })
  },
  components: {},
  data() {
    return {
      form: {
        title: "",
        img: null,
        status: 1
      },
      imgUrl: ""
    };
  },
  methods: {
    //验证
    checked() {
      return new Promise((resolve, reject) => {
        //验证数据是否均不为空
        if (this.form.title === "") {
          alertWarning("标题不能为空");
          return;
        }
        if (this.imgUrl === "") {
          alertWarning("图片不能为空");
          return;
        }
        resolve();
      });
    },
    changeFile(e) {
      let file = e.target.files[0];
      if (file.size > 2 * 1024 * 1024) {
        alertWarning("文件不能超过2M");
        return;
      }
      let imgArr = [".jpg", ".png", ".gif", ".jpeg"];

      if (!imgArr.includes(file.name.slice(file.name.lastIndexOf(".")))) {
        alertWarning("请上传正确的图片");
        return;
      }
      this.imgUrl = URL.createObjectURL(file);
      this.form.img = file;
    },
    ...mapActions({
      requsetLoopList: "loop/requsetLoopList"
    }),

    empty() {
      this.form = {
        title: "",
        img: null,
        status: 1
      };
      this.imgUrl = "";
    },
    update() {
       this.checked().then(() => {
          let data = new FormData();
      for (let i in this.form) {
        data.append(i, this.form[i]);
      }
      reqLoopUpdate(data).then(res => {
        if (res.data.code === 200) {
          this.empty();
          this.cancel();
          this.requsetLoopList();
        }
      });
       })
     
    },
    add() {
      this.checked().then(() => {
        let data = new FormData();
        for (let i in this.form) {
          data.append(i, this.form[i]);
        }
        reqLoopAdd(data).then(res => {
          if (res.data.code === 200) {
            alertSuccess(res.data.msg);
            this.empty();
            this.cancel();
            this.requsetLoopList();
          }
        });
      });
    },
    cancel() {
      this.info.isShow = false;
    },
    getOne(id) {
      reqLoopListOne(id).then(res => {
        if (res.data.code === 200) {
          this.form = res.data.list;
          this.form.id = id;
          this.imgUrl = this.$imgPre + this.form.img;
        }
      });
    }
  },
  mounted() {}
};
</script>
<style scoped>
.line {
  display: flex;
}
.ipt-box {
  flex: 1;
}
.my-upload {
  width: 100px;
  height: 100px;
  border: 1px dashed #ccc;

  position: relative;
}
.my-upload h3 {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 100px;
  color: #666;
}
.my-upload .ipt {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: 2;
  cursor: pointer;
}
.my-upload img {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}
</style>


